.card {
    margin-top: 40px;
    &_row {
        display: flex;
        align-items: flex-start;
    }
    &_photo {
        width: 330px;
    }
    &_item {
        border-radius: 10px;
        img {
            object-fit: cover;
            border-radius: 10px;
            margin: auto;
        }
    }
    &__slider {
        width: 330px;
        height: 300px;
        border-radius: 10px;
    }
    &__nav {
        margin-top: 15px;
        .slick-track {
            display: flex;
            align-items: center;
        }
        &--item {
            width: 50px;
            height: 50px;
            border-radius: 5px;
            cursor: pointer;
            margin: 0 8px;
            img {
                object-fit: cover;
                border-radius: 3px;
            }
        }
    }
    &_text {
        margin-left: 30px;
        &--title {
            
        }
        &--row {
            display: flex;
            align-items: flex-start;
        }
        &--gray {
            ul {
                
            }
            li {
                &.last {
                    
                }    
            }
        }
        &--block {
            
        }
        &--line {
            display: flex;
            align-items: center;
            margin-bottom: 15px;
        }
        &--p {
            
        }
        &--calc {
            
        }
        &--red {
            
        }
        &--flex {
            margin-top: 5px;
        }
        &--bottom {
            border-top: 1px solid #F7F7F7;
            margin-top: 15px;
            padding-top: 6px;
        }
        &--trig {
            display: flex;
            align-items: center;
            &__p {
                margin-left: 14px;
                font-size: 14px;
                color: #000;
            }
        }
    }
}